<template>
    <div class="charts-page">
        <div class="top-card">
            <techo-card resizeEvent="topCard" @onUpLevel="handleUpLevel" :maxCard="true" :upLevel="isShowLevelBtu">
                <div slot="header">境外人员</div>
                <div id="TopBar" style="height:100%"></div>
            </techo-card>
        </div>
        <el-row class="bottom-card" :gutter="10">
            <el-col :span="6">
                <techo-card resizeEvent="FristCard" :maxCard="true">
                    <div slot="header">来华目的</div>
                    <div id="FristPie" style="height:100%"></div>
                </techo-card>
            </el-col>
            <el-col :span="6">
                <techo-card resizeEvent="SecondCard" :maxCard="true">
                    <div slot="header">国籍地区</div>
                    <div id="SecondPie" style="height:100%"></div>
                </techo-card>
            </el-col>
            <el-col :span="6">
                <techo-card resizeEvent="ThirdCard" :maxCard="true">
                    <div slot="header">宗教信仰</div>
                    <div id="ThirdPie" style="height:100%"></div>
                </techo-card>
            </el-col>
            <el-col :span="6">
                <techo-card resizeEvent="FourthCard" :maxCard="true">
                    <div slot="header">职业类别</div>
                    <div id="FourthPie" style="height:100%"></div>
                </techo-card>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import barOptions from "./config/barOptions.js";
import pieOptions from "./config/pieOptions.js";
import service from "./services/infact_people_service.js";
export default {
  data() {
    return {
      DISCODE: "6102",
      // 境外人员分布
      topBarOptions: {},
      topBarChart: null,
      // 来华目的比例占比
      FristPieOptions: {},
      FristPieChart: null,
      // 国籍地区比例占比
      SecondPieOptions: {},
      SecondPieChart: null,
      // 宗教信仰占比
      ThirdPieOptions: {},
      ThirdPieChart: null,
      // 职业类别占比
      FourthPieOptions: {},
      FourthPieChart: null,
      // 当前柱状图级别code
      byCode: "",
      isShowLevelBtu: true,
      COLORS: ["#3965c3", "#ffa639", "#f75a51"]
    };
  },
  watch: {
    byCode(value) {
      this.isShowLevelBtu = value.length == 6 ? false : true;
    }
  },
  mounted() {
    let that = this;
    // 境外人员分布
    this.topBarChart = this.$echarts.init(this.$el.querySelector("#TopBar"));
    // 来华目的比例占比
    this.FristPieChart = this.$echarts.init(
      this.$el.querySelector("#FristPie")
    );
    // 国籍地区比例占比
    this.SecondPieChart = this.$echarts.init(
      this.$el.querySelector("#SecondPie")
    );
    // 宗教信仰占比
    this.ThirdPieChart = this.$echarts.init(
      this.$el.querySelector("#ThirdPie")
    );
    // 职业类别占比
    this.FourthPieChart = this.$echarts.init(
      this.$el.querySelector("#FourthPie")
    );
    this.topBarChart.on("click", val => {
      if (this.byCode.length != 14) {
        this.getPeopleData(val.data.rawdata.code);
        this.getReasonPieData(val.data.rawdata.code);
        this.getNationPieData(val.data.rawdata.code);
        this.getReligionPieData(val.data.rawdata.code);
        this.getTypePieData(val.data.rawdata.code);
      }
    });
    // 初始化统计图自适应函数
    this.$bus.$on("topCard", () => {
      that.topBarChart.resize();
    });
    this.$bus.$on("FristCard", () => {
      that.FristPieChart.resize();
    });
    this.$bus.$on("SecondCard", () => {
      that.SecondPieChart.resize();
    });
    this.$bus.$on("ThirdCard", () => {
      that.ThirdPieChart.resize();
    });
  },
  created() {
    this.getPeopleData();
    this.getReasonPieData();
    this.getNationPieData();
    this.getReligionPieData();
    this.getTypePieData();
  },
  methods: {
    // 境外人员
    getPeopleData(disCode) {
      let param = {
        districtCode: disCode || this.DISCODE,
        entityNames: "OverseasPeople"
      };
      let that = this;
      this.topBarOptions = _.cloneDeep(barOptions);
      service.requestByEntityNames(param, data => {
        if (data.length == 0) return;
        this.byCode = data[0].code;
        this.topBarOptions.yAxis.name = "人数";
        this.topBarOptions.legend.data = ["人数"];
        this.topBarOptions.series.push({
          name: "人数",
          type: "bar",
          itemStyle: {
            normal: {
              color: that.COLORS[Math.floor(Math.random() * 3)]
            }
          },
          barMaxWidth: 80,
          data: []
        });
        _.map(data, (value, index) => {
          that.topBarOptions.xAxis.data.push(value.name);
          this.topBarOptions.series[0].data.push({
            rawdata: value,
            value: value.count
          });
        });
        this.topBarChart.setOption(that.topBarOptions);
        this.topBarChart.resize();
      });
    },
    // 来华目的
    getReasonPieData(disCode) {
      let param = {
        districtCode: disCode || this.DISCODE,
        entityName: "OverseasPeople",
        fieldName: "comingToChina"
      };
      let that = this;
      this.FristPieOptions = _.cloneDeep(pieOptions);
      service.requestByFieldName(param, data => {
        that.FristPieOptions.legend.data = _.keys(_.groupBy(data, "name"));
        that.FristPieOptions.title.text = "来华目的";
        that.FristPieOptions.series[0].name = "境外人员";
        data = data.filter(item => item.count != 0);
        _.map(data, item => {
          that.FristPieOptions.series[0].data.push({
            name: item.name,
            value: item.count
          });
        });
        that.FristPieOptions.series[0].radius = "45%";
        that.FristPieOptions.series[0].center = ["70%", "60%"];
        that.FristPieChart.setOption(that.FristPieOptions);
        that.FristPieChart.resize();
      });
    },
    // 国籍地区
    getNationPieData(disCode) {
      let param = {
        districtCode: disCode || this.DISCODE,
        entityName: "OverseasPeople",
        fieldName: "nationality"
      };
      let that = this;
      this.SecondPieOptions = _.cloneDeep(pieOptions);
      service.requestByFieldName(param, data => {
        that.SecondPieOptions.legend.data = _.keys(_.groupBy(data, "name"));
        that.SecondPieOptions.title.text = "国籍地区";
        that.SecondPieOptions.series[0].name = "境外人员";
        data = data.filter(item => item.count != 0);
        _.map(data, item => {
          that.SecondPieOptions.series[0].data.push({
            name: item.name,
            value: item.count
          });
        });
        that.SecondPieOptions.series[0].radius = "45%";
        that.SecondPieOptions.series[0].center = ["70%", "60%"];
        that.SecondPieChart.setOption(that.SecondPieOptions);
        that.SecondPieChart.resize();
      });
    },
    // 宗教信仰
    getReligionPieData(disCode) {
      let param = {
        districtCode: disCode || this.DISCODE,
        entityName: "OverseasPeople",
        fieldName: "religiousBelief"
      };
      let that = this;
      this.ThirdPieOptions = _.cloneDeep(pieOptions);
      service.requestByFieldName(param, data => {
        that.ThirdPieOptions.legend.data = _.keys(_.groupBy(data, "name"));
        that.ThirdPieOptions.title.text = "宗教信仰";
        that.ThirdPieOptions.series[0].name = "境外人员";
        data = data.filter(item => item.count != 0);
        _.map(data, item => {
          that.ThirdPieOptions.series[0].data.push({
            name: item.name,
            value: item.count
          });
        });
        that.ThirdPieOptions.series[0].radius = "45%";
        that.ThirdPieOptions.series[0].center = ["70%", "60%"];
        that.ThirdPieChart.setOption(that.ThirdPieOptions);
        that.ThirdPieChart.resize();
      });
    },
    // 职业类别
    getTypePieData(disCode) {
      let param = {
        districtCode: disCode || this.DISCODE,
        entityName: "OverseasPeople",
        fieldName: "occupationType"
      };
      let that = this;
      this.FourthPieOptions = _.cloneDeep(pieOptions);
      service.requestByFieldName(param, data => {
        that.FourthPieOptions.legend.data = _.keys(_.groupBy(data, "name"));
        that.FourthPieOptions.title.text = "职业类别";
        that.FourthPieOptions.series[0].name = "境外人员";
        data = data.filter(item => item.count != 0);
        _.map(data, item => {
          that.FourthPieOptions.series[0].data.push({
            name: item.name,
            value: item.count
          });
        });
        that.FourthPieOptions.series[0].radius = "45%";
        that.FourthPieOptions.series[0].center = ["70%", "60%"];
        that.FourthPieChart.setOption(that.FourthPieOptions);
        that.FourthPieChart.resize();
      });
    },
    /** 返回上一级 */
    handleUpLevel() {
      let districtCode = "";
      switch (this.byCode.length) {
        case 9:
          districtCode = this.byCode.slice(0, 4);
          break;
        case 12:
          districtCode = this.byCode.slice(0, 6);
          break;
        case 14:
          districtCode = this.byCode.slice(0, 9);
          break;
        default:
          break;
      }
      this.getPeopleData(districtCode);
      this.getReasonPieData(districtCode);
      this.getNationPieData(districtCode);
      this.getReligionPieData(districtCode);
      this.getTypePieData(districtCode);
    }
  }
};
</script>

<style lang="scss">
.charts-page {
  padding: 10px 5px;
  .top-card {
    height: 23rem;
  }
  .bottom-card {
    padding-top: 0.6rem;
    position: absolute;
    height: calc(100% - 24.2rem);
    width: 100%;
    .el-col-6 {
      height: 100%;
    }
  }
}
</style>
